import * as React from 'react';
import { TimeTablesSettings } from "./TimeTablesForm";
import generateTimeTables, { GenerationFailure, TimeInterval } from "./TimeTablesGenerator";
import { Either } from "../functional-helpers/Either";
import { PracticeSummary } from "./PracticeSummary";
import { InvalidTimeTablesSettings } from "./InvalidTimeTablesSettings";
import { StartPractice } from "./StartPractice";

interface TimeTablesPracticeState {
  timeIntervals: Either<GenerationFailure, TimeInterval[]>;
  startPractice: boolean
}

class TimeTablesPractice extends React.Component<TimeTablesSettings, TimeTablesPracticeState> {
  constructor(props: TimeTablesSettings, state: TimeTablesSettings){
    super(props, state);
    const timeIntervals = generateTimeTables(props);
    this.state = { timeIntervals, startPractice: false };
  }

  public render() {
    return this.state.timeIntervals.fold(
      failure => <InvalidTimeTablesSettings failure={failure}/>,
      timeIntervals => {
        if (this.state.startPractice) {
          return <StartPractice timeIntervals={timeIntervals} onCancel={this.cancelPractice}/>
        } else {
          return <PracticeSummary timeIntervals={timeIntervals} onStart={this.startPractice}/>
        }
      }
    );
  }

  public cancelPractice = () => {
    console.log("WFT is practice not stopping?")
  };
  public startPractice = () => {
    this.setState({ ...this.state, startPractice: true})
  }
}

export default TimeTablesPractice;
